.products {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.products__header {
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    @include fonts (34px, 1.15, 0.04em);
    text-align: center;
    text-transform: uppercase;
    color: $accent-title-color;
    @media screen and (max-width: 479px) {
        font-size: calc(7px + 27 * (100vw / 479));       
    }
    @include mq(tablet) {
        @include fonts (48px, 1.15, 0.04em);
    }
    @include mq(desktop) {
        @include fonts (58px, 1.14, 0.06em);
    }
}

.products__header span {
    @include fonts (18px, 1.16, null);
    color: $upper-title-color;
    margin-bottom: 15px;
    @media screen and (max-width: 479px) {
            font-size: calc(8px + 10 * (100vw / 479));
        }
    @include mq(tablet) {
        @include fonts (26px, 1.15, null);
        margin-bottom: 18px;
    }
    @include mq(desktop) {
        @include fonts (30px, 1.13, 0.04em);
        margin-bottom: 16px;
    }
}
.products__container {
    margin-top: 148px;
    @include mq(tablet) {
        margin-top: 154px;
    }
    @include mq(desktop) {
        margin-top: 258px;
    }
}
.products__list {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100vw;
    @include mq(tablet) {
        flex-direction: row;
        justify-content: center;
    }
        
}

.products__item {
    position: relative;
    border-radius: 24px;
    background: $portfolio-bgc-cream;
    margin-bottom: 140px;
    width: 100%;
    height: 100%;
    @include mq(mobile) {
        width: 280px;
        height: 489px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    @include mq(tablet) {
            margin-right: 16px;
            margin-bottom: 0;
            width: 224px;
            height: 473px;
            &:last-child {
                margin-right: 0;
            }
        }
    @include mq(desktop) {
        margin-right: 32px;
        margin-bottom: 0;
        width: 395px;
        height: 535px;
        &:last-child {
            margin-right: 0;
        }
    }
    &:nth-child(2n){
        background: $portfolio-bgc-coffee;
    }
    &:last-child {
        background: $portfolio-bgc-milkshake;
    }
    
}

.products__item::before {
    display: flex;
    content: "";
    width: 280px;
    height: 280px;
    position: absolute;
    top: -28%;
    left: 50%;
    transform: translateX(-50%);
    background-repeat: no-repeat;
    background-size: cover;

    // transition-property: background-color;
    transition-duration: 400ms;
    transition-timing-function: var(--cubik);

}
.cream::before {
    content: "";
    background-image: url(../images/ice-cream-mb.png);
    // background-image: -webkit-image-set(url(../images/ice-cream-mb.webp) x1);
    
    @include mq(retina) {
        background-image: url(../images/ice-cream-mb@2x.png);
        // background-image: -webkit-image-set url(../images/ice-cream-mb@2x.webp) x2;
    }
    @include mq(tablet) {
        width: 224px;
        height: 224px;
        background-image: url(../images/ice-cream-tb.png);
        // background-image: -webkit-image-set url(../images/ice-cream-tb.webp) x1;
        @include mq(retina) {
            background-image: url(../images/ice-cream-tb@2x.png);
            // background-image: -webkit-image-set url(../images/ice-cream-tb@2x.webp) x2;
        }        
    }
    @include mq(desktop) {
        width: 394px;
        height: 394px;
        position: absolute;
        top: -50%;
        left: 50%;
        transform: translateX(-50%);
        background-image: url(../images/ice-cream-ds.png);
        // background-image:  -webkit-image-set url(../images/ice-cream-ds.webp) x1;
        @include mq(retina) {
            background-image: url(../images/ice-cream-ds@2x.png);
            // background-image: -webkit-image-set url(../images/ice-cream-ds@2x.webp) x2;
        }
    }
}
