.products__modal {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 10px;
    width: 100%;
    height: 100%;
    background-color: rgba(33, 150, 243, 0.9);
    // font-size: 14px;
    // font-weight: 400;
    // line-height: 1.56;
    // letter-spacing: .03em;
    // color: var(--white-color);
    position: absolute;
    top: 0;
    right: 0;
    transform: translateX(101%);
    transition-property: transform;
    transition-duration: 250ms;
    transition-timing-function: var(--cubik);
    opacity: 0;
    visibility: hidden;
}





















.products__card {
    font-style: normal;
    text-align: center;
    color: $hero-uptitle-color;
    width: 100%;
    height: 100%;
    @include mq(mobile-only) {
            padding-top: 160px;
            padding-bottom: 40px;
        }
    
    @include mq(tablet) {
        padding-top: 118px;
        padding-bottom: 40px;
    }
    @include mq(desktop) {
        padding-top: 165px;
        padding-bottom: 60px;
    }

}
.products__card-header {
    font-family: 'Titan One';
    font-weight: 400;
    text-transform: uppercase;
    @include fonts (20px, 1.15, 0.04em);
    @include mq(desktop) {
        @include fonts (30px, 1.13, 0.04em);
    }
}
.products__card-header::after {
    display: block;
    content: '...';
    margin-top: 32px;
}
.products__card-text {
    // font-family: 'DM Sans';
    font-weight: 700;
    @include fonts (14px, 1.52, 0.04em);
    margin-top: 32px;
    margin-bottom: 32px;
    @include mq(tablet) {
        line-height: 1.76;
    }
    @include mq(desktop) {
        font-size: 16px;
        margin-bottom: 60px;
    }
}
.products__btn {
    padding: 15px;
    border-radius: 50%;
    border: none;
    fill: $ligth-mode-btn;
}